body{
    overflow-x: hidden;

}
nav{
    width: 100%;
    background: rgb(30,129,176);
    display: flex;
    overflow: hidden;
    align-items: center;
    padding:1.2rem 2.5rem;
    justify-content: space-around;
}

.search{
    background: #ffffff;
    border: none;
    border: .2px solid rgba(0, 0, 0, 0.184);
    margin:0px 5.5px;
    border-radius:0.4rem;
    outline: none;
    padding:18px;
    font-size: 18px;
}

.card{
    width: 25rem;
    background-color: rgb(255, 255, 255);
    display:flex;
    height:25rem;
    border-radius:0.2rem;
    /* background: url("https://images.unsplash.com/photo-1592601250984-da0dc45e25f7?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=387&q=80"); */
    background-position: center;
    background-size: cover;
    color: white;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
/* <!-- HTML !--> */

/* CSS */
.button-36 {
  background-image: linear-gradient(92.88deg, #455EB5 9.16%, #5643CC 43.89%, #673FD7 64.72%);
  border-radius: 8px;
  border-style: none;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  flex-shrink: 0;
  font-family: "Inter UI","SF Pro Display",-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif;
  font-size: 13px;
  font-weight: 500;
  height: 4rem;
  padding: 0 .6rem;
  text-align: center;
  text-shadow: rgba(0, 0, 0, 0.25) 0 3px 8px;
  transition: all .5s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-36:hover {
  box-shadow: rgba(80, 63, 205, 0.5) 0 1px 30px;
  transition-duration: .1s;
}

@media (min-width: 768px) {
  .button-36 {
    padding: 0 2.6rem;
  }
}

.flexx{
    padding-top: 20px;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
